@import url(https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,400;0,700;1,400&display=swap);
.app-loading{margin-top:70px;width:100%}.app-loading__loader{position:relative;width:80px;height:80px;margin:auto}.app-loading__loader div{position:absolute;border:4px solid #fff;opacity:1;border-radius:50%;animation:lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite}.app-loading__loader div:nth-child(2){animation-delay:-0.5s}@keyframes lds-ripple{0%{top:36px;left:36px;width:0;height:0;opacity:1}100%{top:0px;left:0px;width:72px;height:72px;opacity:0}}

:root{--background: #70B014;--primary: #251919;--secondary: #3B3B3B;--terciary: #F55D2F;--black: #000000;--white: #ffffff}body *{font-weight:400;font-family:'Ubuntu', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscal;-webkit-overflow-scrolling:touch;box-sizing:border-box;color:#251919;color:var(--primary)}a{text-decoration:none}strong{font-weight:700}html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;vertical-align:baseline}ol,ul{list-style:none}html,body,#root,main{background-color:#70B014;background-color:var(--background)}

